#illustration {
  padding: 5rem;
  background-color: $body-color;
  position: relative;
  overflow: hidden;
  width: 100%;
  .container {
    height: 100%;
    width: 100%;
    padding-top: 1rem;
    overflow: hidden;
    .heading {
      h1 {
        font-size: 6rem;
        font-family: "Montserrat", sans-serif;
        font-weight: 600;
        text-transform: uppercase;
        text-align: center;
        opacity: 1;
        position: relative;
        z-index: 4;
        &::before {
          content: "Graphic & Vector";
          font-size: 1.3rem;
          position: absolute;
          top: -1rem;
          left: 0;
          font-family: "Montserrat", sans-serif;
          text-transform: uppercase;
          width: 100%;
          text-align: center;
          font-weight: 600;
          letter-spacing: 0.8rem;
        }
      }
    }
    .illustrationWrapper {
      margin-top: 4rem;
      gap: 4rem;
      height: 100%;
      min-width: 300%;
      z-index: 1;
      display: flex;
      width: auto;
      flex-shrink: 0;
      flex-wrap: nowrap;
      animation: marquee 30s linear 0s infinite alternate-reverse;
      animation-play-state: running;
      &:hover {
        animation-play-state: paused;
      }
    }

    .imageBox {
      margin-top: 4rem;
      width: 100%;

      img {
        height: 340px;
        object-fit: cover;
        width: 300px;
      }
    }

    .hKjfKh,
    .AApyK,
    .lcyjPN {
      gap: 4rem;
      display: flex;
    }
  }
}

@keyframes marquee {
  0% {
    transform: translateX(0);
  }

  100% {
    transform: translateX(-94%);
  }
}
@keyframes marquee2 {
  0% {
    transform: translateX(0);
  }

  100% {
    transform: translateX(-210%);
  }
}
@keyframes marquee3 {
  0% {
    transform: translateX(0);
  }

  100% {
    transform: translateX(-250%);
  }
}

@media all and (max-width: 680px) {
  #illustration .container .heading h1 {
    font-size: 5rem;
  }
  #illustration .container .illustrationWrapper {
    animation-duration: 30s;
    animation-name: marquee3;
  }

  #illustration .container .imageBox img {
    height: 200px;
    object-fit: cover;
    width: 148px;
  }
}

@media all and(min-width:768px) and(max-width: 924px) {
  #illustration .container .heading h1 {
    font-size: 7rem;
  }
  #illustration .container .illustrationWrapper {
    animation-duration: 30s;
    animation-name: marquee2;
  }
}
