/* Main section styling */

#content {
  height: 100vh;
  width: 100%;
  background: $accent-color;
  & .section {
    height: 100%;
    width: 100%;
    padding: 0 15rem;
    & .home {
      height: 100%;
      width: 100%;
      display: grid;
      grid-template-areas: "hero hero" "title bio";
      .hero {
        grid-area: hero;
        background: $body-color;
        display: flex;
        align-items: center;
        padding-top: 6rem;
        justify-content: space-around;
        span {
          position: relative;
          img {
            height: 35rem;
            width: 35rem;
            object-fit: cover;
            border-radius: 30% 70% 70% 30% / 30% 30% 70% 70%;
          }
          &::before {
            content: "";
            position: absolute;
            top: -2rem;
            left: -2rem;
            height: 36rem;
            width: 36rem;
            background: rgba(0, 0, 0, 0.08);
            border-radius: 69% 31% 46% 54% / 52% 60% 40% 48%;
            z-index: -1;
          }
        }
        div {
          .waterMark {
            font-size: 7.6rem;
            opacity: 0.1;
            color: $secondary-color;
            font-weight: 600;
            font-family: "montserrat", sans-serif;
            padding-bottom: 2rem;
          }
        }
      }
      .title {
        grid-area: title;
        width: 70%;
        overflow: hidden;
        .heading {
          font-family: "poppins", sans-serif;
          font-size: 2.6rem;
          font-weight: 500;
          overflow: hidden;
        }
        .para {
          color: $secondary-color;
          padding-top: 1rem;
          font-family: "poppins", sans-serif;
          font-size: 1.6rem;
        }
        a {
          font-size: 3.4rem;
          color: $secondary-color;
          margin-right: 1rem;
          transition: color 0.4s ease;
        }

        .fb_link:hover {
          color: blue;
        }
        .linkedin_link {
          margin-right: 1.8rem;
          &:hover {
            color: rgb(82, 82, 255);
          }
        }
      }
      .bio {
        grid-area: bio;
        .name {
          position: relative;
          h1 {
            position: absolute;
            color: $secondary-color;
            top: -6rem;
            left: -6rem;
            font-size: 8.2rem;
            font-weight: 500;
            font-family: "poppins", sans-serif;
          }
          span {
            position: absolute;
            color: $secondary-color;
            top: 0.4rem;
            left: 9rem;
            font-size: 7.2rem;
            font-weight: 500;
            font-family: "poppins", sans-serif;
          }
        }
        .links {
          position: relative;
          top: 10rem;
          left: 0;
          font-size: 1.6rem;
          font-weight: 600;
          font-family: "poppins", sans-serif;
          position: relative;
          @media screen and (min-width: 375px) and (max-width: 667px) {
            top: 40rem !important;
          }
          &::after {
            content: "";
            position: absolute;
            top: 100%;
            left: 0;
            height: 0.2rem;
            width: 100%;
            background-color: $secondary-color;
          }
          a {
            color: $secondary-color;
            padding: 0 0.4rem 0 0.4rem;
            transition: all 0.4s ease;
            &:hover {
              color: #666666;
            }
          }
        }
      }
    }
  }
}

/*Art Section styling */

#Art {
  height: 100vh;
  width: 100%;
  background-color: #f7f8fb;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  .container {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    .scroll_text {
      text-align: center;
      font-size: 10rem;
      line-height: 8rem;
      font-family: "poppins", sans-serif;
      font-weight: 700;
      text-transform: uppercase;
      position: relative;
      white-space: nowrap;
      & span {
        -webkit-text-fill-color: transparent;
        -webkit-text-stroke: 0.2rem $secondary-color;
      }
    }
  }
}

@import "./Bio.scss";

/* Animation */

@keyframes glowing {
  0% {
    background-position: 0 0;
  }
  50% {
    background-position: 400% 0;
  }
  100% {
    background-position: 0 0;
  }
}

// PHONE

@media all and (max-width: 730px) {
  #content {
    height: 90vh;
  }
  #content .section {
    padding: 0 2rem;
    & .home {
      grid-template-areas: "hero hero" "title title";
    }
  }

  #content .section .home .title {
    margin-top: 2.6rem;
    width: 100%;
    & .heading {
      font-size: 4rem;
    }
    & .para {
      font-size: 2.4rem;
    }
    & .social_links {
      margin-top: 2rem;
    }
    & .social_links > a {
      font-size: 5.4rem;
    }
  }
  #content .section .home .bio {
    display: none;
  }

  #content .section .home .hero {
    grid-area: hero;
    padding: 10rem 0 4rem 0;
    flex-direction: column;
  }
  #content .section .home .hero .waterMark {
    display: none;
  }

  #Art {
    height: 40vh;
  }
  #Art .container .scroll_text {
    font-size: 6rem;
  }
  #Art {
    display: none;
  }
}

//ipad
@media all and(min-width:732px) and(max-width: 1023px) {
  #content {
    height: 100vh;
  }
  #content .section {
    padding: 0 5rem;
  }

  #content .section .home .hero {
    grid-area: hero;
    padding: 8rem 0;
    flex-direction: column;
  }
  #content .section .home .hero .waterMark {
    display: none;
  }

  #Art {
    height: 60vh;
  }
  #Art .container .scroll_text {
    font-size: 7rem;
  }
  #Art {
    display: none;
  }
}

@media screen and (min-width: 411px) and (max-width: 731px) {
  #content .section .home .title {
    margin-top: 2.6rem;
    width: 100%;
  }

  #content .section .home .bio .links {
    top: 28rem;
  }
  #Art {
    display: none;
  }
}
