.photography_section {
  padding: 5rem;
  display: flex;
  flex-direction: column;
  width: 94%;
  margin: auto;
  height: auto;
  h4 {
    font-size: 1.8rem;
    font-family: "Montserrat", sans-serif;
    text-transform: uppercase;
    font-weight: 500;
    letter-spacing: 0.4rem;
  }
  h2 {
    font-size: 8rem;
    font-family: "poppins", sans-serif;
    font-weight: 600;
    text-transform: uppercase;
  }
  .photo_container {
    display: grid;
    padding-top: 1rem;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: auto;
    gap: 2rem;
    // background: hsl(201, 21%, 97%);
    @media all and (max-width: 520px) {
      grid-template-columns: repeat(1, 1fr);
    }
    @media all and(min-width:522px) and (max-width: 780px) {
      grid-template-columns: repeat(2, 1fr);
    }

    .imageBox {
      width: 100%;
      img {
        object-fit: contain;
        height: auto;
        width: 100%;
        transition: all 0.4s ease;
        &:hover {
          filter: saturate(1.2) brightness(120%);
        }
      }
      h1 {
        width: 100%;
        margin-top: 0.8rem;
        text-align: center;
        font-size: 2.4rem;
        font-family: "poppins", sans-serif;
        font-weight: 500;
        text-transform: capitalize;
      }
      h2 {
        width: 100%;
        text-align: center;
        font-size: 1.6rem;
        font-family: "Montserrat", sans-serif;
        font-weight: 500;
        text-transform: uppercase;
        letter-spacing: 0.4rem;
      }
    }
  }
}

@media all and (max-width: 320px) {
  .photography_section h2 {
    font-size: 6rem;
  }
}
