#bio {
  height: 100vh;
  width: 100%;
  padding: 0 5rem;
  background: black;
  position: relative;
  &::before {
    content: "";
    position: absolute;
    background: url("../logo/Mask.jpg");
    background-attachment: fixed;
    background-size: cover;
    background-repeat: no-repeat;
    opacity: 0.4;
    filter: contrast(120%);
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    z-index: 1;
  }
  .bioContainer {
    height: 100vh;
    width: 100%;
    position: relative;
    z-index: 2;
    .bio-wrapper {
      height: 100%;
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
      h2 {
        width: 50%;
        text-align: center;
        font-size: 3rem;
        padding-left: 1rem;
        font-family: "poppins", sans-serif;
        font-weight: 400;
        color: $light-color;
      }
      .imageBox {
        height: auto;
        width: 20rem;
        background-size: cover;
        img {
          object-fit: contain;
          width: 100%;
          height: 100%;
          filter: brightness(90%) contrast(120%);
        }
      }
    }
  }
}

@media screen and (max-width: 768px) {
  #bio {
    display: none;
  }
}

@media screen and(min-width:768px) and(max-width: 1023px) {
  #bio {
    display: none;
  }
}
