#error {
  height: 100vh;
  width: 100%;
  display: grid;
  place-items: center;
  .container {
    display: flex;
    flex-direction: column;
    align-items: center;
    h1 {
      font-size: 6rem;
      font-family: "poppins", sans-serif;
      font-weight: 500;
    }
    p {
      font-size: 3rem;
      font-family: "poppins", sans-serif;
      font-weight: 500;
    }
    a {
      background: none;
      z-index: 4;
      color: $secondary-color;
      font-size: 2rem;
      font-weight: 500;
      padding: 1rem 3rem;
      margin-top: 2rem;
      position: relative;
      border-bottom: 0.2rem solid $OxfordBlue;
      -webkit-transition: -webkit-box-shadow 0.8s ease,
        -webkit-transform 0.8s ease;
      transition: -webkit-box-shadow 0.8s ease, -webkit-transform 0.8s ease;
      transition: box-shadow 0.8s ease, transform 0.8s ease;
      &:hover {
        color: $light-color;
        -webkit-box-shadow: inset 0 -10rem $OxfordBlue;
        box-shadow: inset 0 -10rem $OxfordBlue;
      }
    }
  }
}
