.about {
  height: 122vh;
  width: 100%;
  font-family: "Poppins", sans-serif;
  position: relative;
  margin-top: 13rem;
  .width {
    max-width: 90%;
    padding: 0 5rem;
    margin: auto;
  }
}

section .title {
  position: relative;
  text-align: center;
  font-size: 6rem;
  font-family: "poppins", sans-serif;
  font-weight: 600;
  text-transform: uppercase;
  padding-bottom: 4rem;
  margin-top: 2rem;
  z-index: 2;
  &::before {
    content: "A few words";
    font-size: 1.3rem;
    position: absolute;
    top: -1rem;
    left: 0;
    font-family: "Montserrat", sans-serif;
    text-transform: uppercase;
    width: 100%;
    text-align: center;
    font-weight: 600;
    letter-spacing: 0.8rem;
  }
}

.about-content {
  & h1 {
    font-size: 10rem;
    font-weight: 400;
  }

  & .about-right {
    width: 66%;
    float: right;

    & p {
      margin-top: 2rem;
      font-size: 2.6rem;
      text-align: justify;
    }
    & .about-sub {
      padding-top: 2rem;
      font-size: 2rem;
    }
  }
}

.tools {
  height: fit-content;
  width: 100%;
  padding: 5rem;
  background: $body-color;
  .container {
    display: flex;
    align-items: center;
    flex-direction: column;
    padding: 4rem;
    background-color: $accent-color;
    .heading {
      font-size: 3rem;
      font-family: "poppins", sans-serif;
      font-weight: 300;
      text-transform: uppercase;
    }
    .content {
      padding-top: 2rem;
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: space-around;
      font-family: "poppins", sans-serif;
      ul li {
        list-style: none;
        font-size: 1.6rem;
        padding: 1.2rem;
        margin: 1rem;
        text-align: center;
        line-height: 1;
        overflow: hidden;

        span {
          display: block;
          font-size: 4rem;
        }
        .html {
          color: #f16529;
        }
        .css {
          color: #1572b6;
        }
        .javascript {
          color: #e9d44a;
        }
        .react {
          color: #61dafb;
        }
        .node {
          color: #699f5b;
        }
        .express {
          color: #699f5b;
        }
        .mongo {
          color: #77b96b;
        }
        .typescript {
          color: #007acc;
        }
        .sass {
          color: #cb6699;
        }
        .bootstrap {
          color: #7952b3;
        }
        .redux {
          color: #7952b3;
        }
        .postman {
          color: #ff6c37;
        }
        .git {
          color: #f03c2e;
        }
        .tailwind {
          color: #49aeb4;
        }
        .xd {
          color: #49aeb4;
        }
        .figma {
          color: #f24e1e;
        }
        .photoshop {
          color: #1abcfe;
        }
        .illustrator {
          color: #f79500;
        }
        .jest {
          color: #99424f;
        }
        .cypress {
          color: #5c5c5e;
        }
      }
    }
  }
}

@media all and (max-width: 640px) {
  .about {
    height: 100vh;
    .width {
      max-width: 100%;
      padding: 0 2rem;
    }
    .about-content {
      & h1 {
        font-size: 8rem;
        text-align: center;
        font-weight: 400;
      }
      & .about-right {
        width: 94%;
        margin: auto;
        float: none;
      }
    }
    .about-content .about-right .about-sub {
      padding: 0;
    }
  }

  .tools {
    height: 84vh;
    padding: 0rem;
  }
}
@media all and(min-width:768px) and(max-width: 1023px) {
  .about {
    .about-content {
      .left {
        width: 38%;
      }
    }
  }
  .tools {
    padding: 0rem;
  }
}
