.contactPage {
  height: 100vh;
  width: 100%;
  padding: 5rem;
  margin-bottom: 10rem;
  position: relative;
  background: $accent-color;
  overflow: hidden;
  .container {
    position: relative;
    top: 35%;
    width: 100%;
    height: 84%;
    display: flex;
    background: hsl(201, 21%, 98%);
    justify-content: space-between;
    border-top: 0.2rem solid $secondary-color;

    z-index: 1;
    .right {
      height: 100%;
      width: 100%;
      padding: 0 4rem;
      font-family: "poppins", sans-serif;
      display: flex;
      justify-content: center;
      flex-direction: column;
      h1 {
        font-family: "Montserrat", sans-serif;
        font-size: 3rem;
        padding-bottom: 2rem;
      }
      .info {
        font-size: 1.8rem;
        width: 90%;
        text-align: justify;
        padding-bottom: 1rem;
      }
      p {
        font-size: 1.6rem;
        padding-bottom: 1rem;
      }
      .mail {
        color: $secondary-color;
      }
      .address {
        font-size: 2rem;
        color: rgb(56, 56, 56);
      }
      .gmail {
        font-size: 2rem;
        color: rgb(56, 56, 56);
      }
      .social {
        padding: 1rem 0;
        width: 50%;
        a {
          margin: 0 3rem 0 0;
          color: rgb(66, 66, 66);
          transition: all 0.4s ease;
        }
        .fb {
          font-size: 4rem;
          &:hover {
            color: blue;
          }
        }
        .linkedIn {
          font-size: 4rem;
          &:hover {
            color: #0077b5;
          }
        }
        .ig {
          font-size: 4rem;
          transition: all 0.4s ease;
          &:hover {
            background: linear-gradient(
              45deg,
              #f09433 0%,
              #e6683c 25%,
              #dc2743 50%,
              #cc2366 75%,
              #bc1888 100%
            );
            -webkit-text-fill-color: transparent;
            background-clip: text;
            -webkit-background-clip: text;
          }
        }
        .git {
          font-size: 4rem;
          &:hover {
            color: $secondary-color;
          }
        }
      }
    }
  }
  &::before {
    content: "Lets Work Together";
    position: absolute;
    top: 20%;
    left: 11%;
    font-size: 6rem;
    font-family: "Montserrat", sans-serif;
    font-weight: 400;
    letter-spacing: 2rem;
    text-transform: uppercase;
    z-index: 1;
  }
}
@media screen and (max-width: 728px) {
  .contactPage::before {
    font-size: 5rem;
  }
  .contactPage {
    margin-bottom: 0rem;
    .container {
      top: 22% !important;
      flex-direction: column;
      height: 82% !important;
      gap: 10rem;
      .left iframe {
        height: 150%;
        width: 100%;
      }
      .right .info,
      .right p {
        font-size: 2rem;
      }
      .right .social {
        width: 62%;
      }
    }
  }
  .contactPage::before {
    top: 10%;
    width: 80%;
  }
}

@media screen and(min-width:728px) and(max-width: 1023px) {
  .contactPage {
    margin-bottom: 5rem;
    height: 120vh;
    .container {
      top: 22% !important;
      flex-direction: column;
      height: 82% !important;
      gap: 10rem;
      .left iframe {
        height: 180%;
        width: 100%;
      }
    }
  }
  .contactPage::before {
    top: 10%;
    width: 80%;
  }
}
