#projects {
  height: 80vh;
  padding: 5rem;
  background: $accent-color;
  .container {
    padding-top: 5rem;
    display: flex;
    justify-content: center;
    .work {
      width: 100%;
      .title {
        font-size: 6rem;
        position: relative;
        &::before {
          content: "Ui/ux designs";
          font-size: 1.3rem;
          position: absolute;
          top: -1rem;
          left: 0;
          font-family: "Montserrat", sans-serif;
          text-transform: uppercase;
          width: 100%;
          text-align: center;
          font-weight: 600;
          letter-spacing: 0.8rem;
        }
      }
      .main {
        display: flex;
        align-items: center;
        padding: 5rem;
        pointer-events: none;
        .heading {
          width: 100%;
          font-size: 3rem;
          font-family: "poppins", sans-serif;
          font-weight: 400;
        }
        .rotate {
          width: 40%;
          text-align: center;

          img {
            height: 20rem;
            width: 20rem;
            object-fit: cover;
            mix-blend-mode: multiply;
            animation: rotation 10s linear infinite;
          }
        }
      }
    }
  }
}

#webDesign {
  background: $body-color;
  height: auto;
  padding: 5rem;
  overflow: hidden;
  .projects {
    width: 100%;
    .wrapper {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      .link {
        color: $secondary-color;
        border-bottom: 0.1rem solid #555;
        width: 48%;
        overflow: hidden;
        list-style: none;
        margin: 2rem 0;
        @media all and (max-width: 640px) {
          width: 100%;
        }

        // position: relative;
        // .floating-image {
        //   pointer-events: none;
        //   width: 40rem;
        //   top: 0;
        //   left: 0;
        //   height: auto;
        //   position: absolute;
        //   background-size: cover;
        //   mix-blend-mode: multiply;
        //   z-index: 99;
        //   overflow: hidden;
        //   img {
        //     position: relative;
        //     width: 100%;
        //     height: 100%;
        //   }
        // }
        .banner {
          pointer-events: none;
          width: 40rem;
          background-size: cover;
          overflow: hidden;
          transition: opacity 350ms ease;
          img {
            width: 100%;
            height: 100%;
            clip-path: inset(100% 0% 0% 0%);
            transition: clip-path 1s cubic-bezier(0.77, 0, 0.175, 1);
            &.is-reveal {
              clip-path: inset(0% 0% 0% 0%);
            }
          }
        }
        .header {
          display: flex;
          justify-content: space-between;
          align-items: center;
          width: 100%;
          overflow: hidden;
          padding-top: 2rem;
          .caption {
            z-index: 100;
            font-size: 2.4rem;
            font-weight: 500;
            font-family: "poppins", sans-serif;
            transform: translateY(100px);
            transition: transform 1s cubic-bezier(0.77, 0, 0.175, 1);
            transition-delay: 0.4s;
            &.is-reveal {
              transform: translateY(0px);
            }
          }
          i {
            z-index: 100;
            font-size: 2.4rem;
            transform: translateY(100px);
            transition: transform 1s cubic-bezier(0.77, 0, 0.175, 1);
            transition-delay: 0.4s;
            &.is-reveal {
              transform: translateY(0px);
            }
          }
        }
        .tags {
          overflow: hidden;
          .desc {
            font-size: 1.8rem;
            position: relative;
            z-index: 100;
            margin-bottom: 0.4rem;
            transform: translateY(100px);
            transition: transform 0.4s cubic-bezier(0.77, 0, 0.175, 1);
            transition-delay: 0.8s;

            &.is-reveal {
              transform: translateY(0px);
            }
          }
          span {
            display: inline-block;
            margin: 0.2rem 0 1.8rem 0;
            font-size: 1.6rem;
            position: relative;
            z-index: 100;
            font-weight: 400;
            font-family: "poppins", sans-serif;
            color: $secondary-color;
            white-space: nowrap;
            transform: translateY(100px);
            transition: transform 0.4s cubic-bezier(0.77, 0, 0.175, 1);
            transition-delay: 1s;

            &.is-reveal {
              transform: translateY(0px);
            }
            span {
              font-weight: 600;
            }
            &:not(:last-child) {
              padding-right: 1.8rem;
              margin-right: 1.2rem;
              position: relative;

              &:after {
                content: "";
                position: absolute;
                height: 0.8rem;
                width: 0.8rem;
                top: 50%;
                right: 0;
                transform: translateY(-50%);
                background-color: $secondary-color;
                border-radius: 10rem;
              }
            }
          }
        }
        .work_btns {
          width: 100%;
          font-size: 1.8rem;
          margin-bottom: 2rem;
          position: relative;
          z-index: 100;
          & a {
            color: $secondary-color;
            margin-right: 2rem;
            transition: all 0.4s ease;
            & i {
              padding-right: 0.4rem;
            }
            &:hover {
              padding: 0.4rem 1rem;
              border-radius: 0.8rem;
              background-color: $secondary-color;
              color: $OxfordBlue;
            }
          }
        }
      }
    }
  }
}

@import "./illustration.scss";
@keyframes rotation {
  0% {
    transform: rotate(360deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

@media all and (max-width: 640px) {
  #projects {
    height: 60vh;
    padding: 2rem;
  }

  #projects .container .work .main .heading {
    font-size: 2.6rem;
  }

  #webDesign {
    height: fit-content;
    .projects .wrapper .link .banner {
      box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
    }
  }
}

@media all and(min-width:768px) and(max-width: 1023px) {
  #work {
    height: 60vh;
  }
  #webDesign {
    height: fit-content;
  }
}
