footer {
  height: 60vh;
  width: 100%;
  padding: 5rem;
  font-family: "poppins", sans-serif;
  // background-color: hsl(201, 21%, 97%);
  hr {
    height: 0.3rem;
    background-color: $secondary-color;
  }
  .footer_container {
    display: flex;
    align-items: center;
    justify-content: space-around;
    height: 100%;
    h1 {
      font-size: 3rem;
      margin-bottom: 1rem;
      font-family: "Montserrat", sans-serif;
    }
    p {
      font-size: 1.6rem;
      font-family: "poppins";
    }
    .mail {
      padding-top: 1rem;
    }
    i {
      font-size: 2rem;
    }
    a {
      font-size: 1.6rem;
      color: $secondary-color;
      width: 100%;
      margin-bottom: 0.4rem;
      font-weight: 500;
      position: relative;
      &::after {
        content: "";
        position: absolute;
        height: 0.3rem;
        width: 100%;
        left: 0;
        top: 90%;
        background-color: $secondary-color;
        transform: scaleX(0);
        transform-origin: right;
        transition: transform 0.3s linear;
      }
      &:hover::after {
        transform: scaleX(1);
        transform-origin: left;
      }
    }
    .contact_info {
      width: 30%;
    }
    .social {
      display: flex;
      flex-direction: column;
      a {
        font-family: "poppins", sans-serif;
        width: 54%;
        & i {
          padding-right: 1rem;
        }
      }
    }
  }
  .footer__content {
    width: 100%;
    text-align: center;
    font-size: 1.8rem;
    text-transform: capitalize;
    font-family: "poppins", sans-serif;
    span a {
      color: $secondary-color;
      font-weight: 600;
    }
  }
}

@media screen and (max-width: 640px) {
  footer {
    height: 50vh;

    .footer_container {
      flex-direction: column;
      align-items: flex-start;
      justify-content: space-evenly;
      height: 100%;
      .contact_info {
        width: 100%;
        & p,
        & .mail > a {
          font-size: 2rem;
        }
      }
      & .social > a {
        font-size: 2rem;
        width: 100%;
      }
    }
  }
}
@media screen and(min-width:768px) and(max-width: 1023px) {
  footer {
    height: 40vh;
    padding: 0;
    hr {
      margin: 0 5rem;
    }
    .footer_container {
      justify-content: space-evenly;
      height: 80%;
      .contact_info {
        width: 50%;
      }
      .social {
        align-items: flex-start;
        justify-content: space-evenly;
      }
    }
  }
}
