@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@200;300;400;500;600&family=Poppins:wght@400;500;600;700&display=swap");
@import "./variables.scss";
@import "./navbar.scss";
@import "./Home.scss";
@import "./footer.scss";
@import "./mobile.scss";
@import "./photography.scss";
@import "./About.scss";
@import "./Error.scss";
@import "./Work.scss";
@import "./Contact.scss";
* {
  padding: 0;
  margin: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  text-decoration: none;
  user-select: none;
}
body {
  overflow-x: hidden;
}

html {
  font-size: 62.5%;
  scroll-behavior: smooth;
}

/* custom scroll bar */

::-webkit-scrollbar {
  width: 1.2rem;
}

::-webkit-scrollbar-track {
  background: $accent-color;
}

::-webkit-scrollbar-thumb {
  border-radius: 5rem;
  background: #888;
}

::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.no-scroll {
  overflow-y: hidden;
}

.smooth-scroll {
  scroll-behavior: smooth !important;
}

.custom-cursor {
  height: 5rem;
  width: 5rem;
  border-radius: 100%;
  position: fixed;
  z-index: 1212121;
  border: 0.2rem solid $OxfordBlue;
  pointer-events: none;
  overflow: hidden;
  transform: translate3d(0, 0, 0);
  @media all and (max-width: 950px) {
    display: none;
  }
}

.left_panel_background,
.right_panel_background {
  height: 100vh;
  width: 50vw;
  position: absolute;
  z-index: 21212121212;
  background-color: #daddd8;
  @media all and (max-width: 640px) {
    width: 100vw;
  }
}

.right_panel_background {
  right: 0;
  background-color: #ecebe4;
  @media all and (max-width: 640px) {
    display: none;
  }
}

/* contact button */

.contactBox a {
  background: rgba(15, 20, 30, 0.3);
  display: block;
  position: fixed;
  width: 4.6rem;
  height: 4.6rem;
  left: 4rem;
  bottom: 4rem;
  z-index: 20;
  padding: 1.3rem;
  border: 0 none;
  border-radius: 2.3rem;
  transition: all 0.2s cubic-bezier(0.3, 0, 0.5, 1) 0.2s;
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
  & i {
    display: block;
    font-size: 2rem;
    color: $light-color;
    float: left;
    margin-right: 0.8rem;
  }
  & span {
    display: block;
    width: 0;
    overflow: hidden;
    font-size: 1.4rem;
    font-weight: 500;
    color: $light-color;
    opacity: 0;
    transition: width 0s linear 0.2s, opacity 0.2s cubic-bezier(0.3, 0, 0.5, 1);
  }
  &:active,
  &:hover {
    background: rgba(0, 0, 0, 0.9);
    width: 14rem;
    transition: all 0.2s cubic-bezier(0.3, 0, 0.5, 1);
  }
  &:active span,
  &:hover span {
    width: auto;
    opacity: 1;
    transition: width 0s linear 0.2s, opacity 0.8s cubic-bezier(0.3, 0, 0.5, 1);
  }
}

/* scroll-up button styling*/

.scroll-up-btn {
  position: fixed;
  height: 5rem;
  width: 5rem;
  background: rgba(0, 0, 0, 0.3);
  right: 5rem;
  bottom: 0rem;
  text-align: center;
  line-height: 4.5rem;
  color: $light-color;
  z-index: 998;
  font-size: 3rem;
  border-radius: 50%;
  cursor: pointer;
  opacity: 0;
  pointer-events: none;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}

.show {
  position: fixed;
  height: 5rem;
  width: 5rem;
  background: rgba(0, 0, 0, 0.3);
  right: 6rem;
  bottom: 4rem;
  text-align: center;
  line-height: 4.5rem;
  color: $light-color;
  z-index: 998;
  font-size: 3rem;
  border-radius: 50%;
  cursor: pointer;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
  opacity: 1;
  pointer-events: auto;
  &:hover {
    background: rgba(0, 0, 0, 0.9);
  }
}

@media all and(min-width:1812px) and(max-width: 2560px) {
  html {
    font-size: 79.5%;
  }
}
